import Vue from "vue"; // No problem.
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./registerServiceWorker";



// Styles and icons
import "bootstrap";
// import "font-awesome";
// import "font-awesome/css/font-awesome.min.css";
import "vue-awesome/icons";

// tslint:disable-next-line:no-var-requires
require("../node_modules/bootstrap/scss/bootstrap.scss");
// tslint:disable-next-line:no-var-requires
require("../node_modules/font-awesome/css/font-awesome.min.css");

// Axios REST API tool

// - BUG: VueAxios incompatible with TypeScript May, 2018.
// `Cannot redeclare block-scoped variable 'Vue' https://github.com/imcvampire/vue-axios/issues/24
// temp fix: skipLibCheck in tsconfig.json
import axios from "axios";
import VueAxios from "vue-axios";
Vue.use(VueAxios, axios);

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: (h) => h(App)
  // jsx alias ... render: createElement => { return createElement(App) }
}).$mount("#app");

// Vue's bootstrap bug hot reload.
// https://github.com/vuejs/vue-hot-reload-api/issues/61
// https://github.com/DanielChan27/webpack-vue-starter/commit/4ab79df63857da84c09704c2d2e455c4961b47fd
